import { getUser} from '../config/session';
import { logOut} from '../config/session';
import { validateUser} from '../config/session';
import { uploadFile} from '../config/requisitions';
import { get} from '../config/requisitions';
import react from 'react';
import BasePage from './BasePage';


function Home(){    
    // validateUser()
    return(
        <BasePage title="Home">       

            Ol {getUser().name} <br/>
            Seu email : {getUser().email}<br/>
            Seu login : {getUser().login}<br/>
            Sua senha : secreta!
            <a href="#" onClick={()=>{
                window.open('/', '_self')
                logOut() 
            }}>Sair</a>
        </BasePage>
    
    )
}

export default Home;